

.App {
  border: 2px solid black;
  text-align: center;
  background: linear-gradient(to left, #e6e6e6,#f5f5f5);
  color: #333;
  margin: 10px;
  min-height: 95vh;
  font-family: Avantgarde, TeX Gyre Adventor, URW Gothic L, sans-serif;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  min-height: 15vh;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  text-align: left;
  justify-content: center;
  font-size: calc(10px + 0.75vmin);
  background: linear-gradient(to left, #e6e6e6,#f5f5f5);
  padding-left: 10px;
  border: 1px #000;
   
}

.App-link {
  color: #000000;
}

.App-footer{
  min-height: 1vh;
  display: inline-flex;
  margin-left: -75px;
  text-align: center;
  position:absolute;                  
  bottom: 4vh;                           
  justify-content: center;
  font-size: calc(10px + 0.75vmin);
}

.contact-icon{
  width: 2em;
  height: 2em;
}
ul.header li {
  display: inline-block;
  padding-right: 5px;
  list-style-type: none;
 
}
ul.header {
  padding: 0;
  margin-top: 1vh;
}
.header{
  font-size: 0.7em;
  text-align: left;
  padding: 15px;
}
ul.header li a {
  color: #000;
  font-weight: bold;
  text-decoration: none;
  margin: -10px;
  
  display: contents;
}
.content h2 {
  padding: 0;
  margin: 0;
}
.content{

  padding: 20vh;
  margin: 1vh;
}

.svg-icon {
  width: 2em;
  height: 2em;
}

.svg-icon path,
.svg-icon polygon,
.svg-icon rect {
  fill: #4691f6;
}

.svg-icon circle {
  stroke: #4691f6;
  stroke-width: 1;
}
